* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(to right, #003166, #66ffeb);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  animation: gradient 15s infinite;
}

img {
  max-width: 100%;
  display: block;
}

.btn {
  text-align: center;
  color: #000;
  cursor: pointer;
  background-color: #337ab7;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.btn:hover {
  background-color: #23527c;
}

.btn:active {
  background-color: #123b5a;
}

.btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(51, 122, 183, .5);
}

ul {
  list-style: none;
}

.name-gallery {
  color: #000;
  font-size: 25px;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

.name-gallery:hover {
  color: #adff2f;
}

